//Side Bar Styles
.showGeneratorContainer {
    display: flex;
}

.searchBox {
    width: 100%;
    background: white;
    border: none;
    color: #577F90;
    border-radius: 25px;
    padding: 15px;
    font-size: 0.9rem;
    margin-bottom: 5px;
    height: 10px;
    font-family: $font;
    font-weight: 400;
    margin-top: 35px;
    margin-left: 7px

}

.sideBarSearchBtn {
    width: 79%;
    background: $coral;
    border: none;
    color: $blue;
    border-radius: 4px;
    padding: 5px;
    font-size: 0.8rem;
    margin-bottom: 15px;
    margin-left: 17px;
    height: 26px;
    font-family: $font;
    font-weight: 700;
    text-transform: uppercase;
}

.queryContainer {
    margin-right: 20px;
}

.criteriaContainer {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.languageContainer {
    color: #577F90;
    border-radius: 25px;
    font-size: 0.9rem;
    font-family: $font;
    font-weight: 700;
    margin-left: 4px;
    text-transform: uppercase;
    margin-top: 10px;
}

.firebase-data h1 {
    font-size: 1.6rem;
    text-transform: uppercase;
    font-family: $font;

}